import React, {useState} from 'react'
import { Card, CardActions, CardContent, CardMedia, Button, Typography, ButtonBase } from "@material-ui/core"
import ThumbUpAltIcon from "@material-ui/icons/ThumbUpAlt"
import ThumbUpAltOutlined from '@material-ui/icons/ThumbUpAltOutlined'
import DeleteIcon from "@material-ui/icons/Delete"
import {CardActionArea} from '@material-ui/core'
import MoreHorizIcon from "@material-ui/icons/MoreHoriz"
import { useHistory } from 'react-router-dom'
import moment from "moment"
import {useDispatch} from "react-redux"

import useStyles from "./styles"
import {deletePost,likePost} from "../../../actions/posts.js"

const Post = ({post, setCurrentId, currentId}) => {
    const classes = useStyles()
    const dispatch = useDispatch()
    const user = JSON.parse(localStorage.getItem('profile'));
    const history = useHistory()
    const [likes, setLikes] = useState(post?.likes)

    const userId = user?.result?.googleId || user?.result?._id
    const hasLikedPost = likes.find((like) => like === (userId))

    const handleLike = () => {
      {dispatch(likePost(post._id))

      if (hasLikedPost) {
          setLikes(post.likes.filter((id) => id !== (userId)))
      } else {
          setLikes([...post.likes, userId])
      }
      }   
    }

    const openPost = () => {
      history.push(`/posts/${post._id}`)
    }

  const Likes = () => {
      if (likes.length > 0) {
        return likes.find((like) => like === (userId))
          ? (
            <React.Fragment><ThumbUpAltIcon fontSize="small" />&nbsp;{likes.length > 2 ? `You and ${likes.length - 1} others` : `${likes.length} like${likes.length > 1 ? 's' : ''}` }</React.Fragment>
          ) : (
            <React.Fragment><ThumbUpAltOutlined fontSize="small" />&nbsp;{likes.length} {likes.length === 1 ? 'Like' : 'Likes'}</React.Fragment>
          );
      }
  
      return <React.Fragment><ThumbUpAltOutlined fontSize="small" />&nbsp;Like</React.Fragment>;
    };

    const handleDelete = (id) => {
      console.log("handle delete")
      dispatch(deletePost(id))
    }

  return (
    <Card className={classes.card} raised elevation={6}>
      <CardActionArea className={classes.cardAction} onClick={openPost} components="span">

      <CardMedia className={classes.media} image={post.selectedFile} title = {post.title} />
      <div className={classes.overlay}>
        <Typography variant='h6'>{post.name}</Typography>
        <Typography variant='body2'>{moment(post.createdAt).fromNow()}</Typography>
      </div>
      {(user?.result?.googleId === post?.creator || user?.result?._id === post?.creator) && (
      <div className={classes.overlay2}>
        <Button style={{color:"white"}} size="small" onClick={() => {setCurrentId(post._id);console.log(currentId)}}>
          <MoreHorizIcon fontSize="medium" />
        </Button>
      </div>
      )}
      <div className={classes.details}>
        <Typography variant='body2' color="textSecondary">{post.tags.map((tag) => `#${tag}`)}</Typography>
      </div>
        <Typography className={classes.title} variant='h5' gutterBottom>{post.title}</Typography>
      <CardContent>
        <Typography variant='body2' color='textSecondary' component="p">{post.message}</Typography>
      </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardActions}>
          <Button size="small" color="primary" disabled={!user?.result} onClick={handleLike}>
            <Likes />
          </Button>
          {(user?.result?.googleId === post?.creator || user?.result?._id === post?.creator) && (
            <Button size="small" color="primary" onClick={()=> dispatch(deletePost(post._id))}>
              <DeleteIcon fontSize="small" />
              Delete
            </Button>
          )}
      </CardActions>
    </Card>
  )
}

export default Post