import React, { useState, useEffect } from "react"
import {TextField, Button, Typography, Paper} from "@material-ui/core"
import FileBase from "react-file-base64"
import { useDispatch, useSelector } from "react-redux"

import {createPost, updatePost} from "../../actions/posts"
import useStyles from "./styles"

const Form = ({currentId, setCurrentId}) => {
    const [postData, setPostData] = useState({title: '', message:'', tags:'', selectedFile:''})
    const post = useSelector((state) => currentId?state.posts.find((p)=> p._id === currentId): null)
    const classes = useStyles()
    const dispatch = useDispatch()
    const user = JSON.parse(localStorage.getItem("profile"))

    useEffect(() => {
        if(post) setPostData(post)
    }, [post])

    const handleSubmit = (e) => {
        e.preventDefault()
        // console.log("postData")
        // console.log(postData)
        // const dt = useSelector((store) => store.posts)
        // console.log(dt)

        console.log("current")
        console.log(currentId)

        if (currentId === null) {
            dispatch(createPost({...postData, name: user?.result?.name}))
        } else {
            dispatch(updatePost(currentId, { ...postData, name: user?.result?.name}))
        }
        clear()
    }

    if (!user?.result?.name) {
        return (
            <Paper className={classes.paper}>
                <Typography variant="h6" align="center">
                    Please Sign In to create your own memories and likes others'.
                </Typography>
            </Paper>
        )
    }

    const clear = () => {
        console.log(currentId)
        console.log("here")
        setPostData({title: '', message:'', tags:'', selectedFile:''})
        setCurrentId(null)
        console.log(currentId)
    }

    // ok //
    // const [count, setCount] = useState(0)
    // const handleClick = () => {
    //     setCount(count+1)
    // }
    //    //

    return (
        <Paper className={classes.paper} elevation={6}>
            <form autoComplete="off" noValidate className={`${classes.root} ${classes.form}`} onSubmit={handleSubmit} >
                <Typography variant="h6">{currentId? "Editing" : "Creating"} a Memory</Typography>
                <TextField  name="title"  variant="outlined"  label="Title" fullWidth value={postData.title} onChange={(e) => setPostData({ ...postData, title: e.target.value })} />
                <TextField  name="message"  variant="outlined"  label="Message" fullWidth value={postData.message} onChange={(e) => setPostData({ ...postData, message: e.target.value })} />
                <TextField  name="tags"  variant="outlined"  label="Tags" fullWidth value={postData.tags} onChange={(e) => setPostData({ ...postData, tags: e.target.value.split(",") })} />
                <div className="classes.fileInput">
                    <FileBase type="file" multiple={false} onDone={({base64}) => setPostData({ ...postData, selectedFile: base64})} />
                </div>
                <Button className={classes.buttonSubmit} variant="contained" color="primary" site="large" type="submit" fullWidth>Submit</Button>
                <Button variant="contained" color="secondary" site="small" onClick={clear} fullWidth>Clear</Button>
            </form>
            {/* <Button onClick={handleClick}>
                Clicked {count} times
            </Button> */}
        </Paper>
    )
}

export default Form